function mango(quantity, price) {
  let freeMango = "";
  let mangoCount = 0;

  if (quantity <= 2) {
    freeMango = "no mango for free";
  } else if (quantity % 3 === 0) {
    mangoCount = quantity / 3;
    if (mangoCount === 1) {
      quantity = quantity - mangoCount;
      freeMango = "+" + mangoCount + " mango for free";
    } else {
      quantity = quantity - mangoCount;
      freeMango = "+" + mangoCount + " mangoes for free";
    }
  } else {
    mangoCount = Math.floor(quantity / 3);
    if (mangoCount === 1) {
      quantity = quantity - mangoCount;
      freeMango = "+" + mangoCount + " mango for free";
    } else {
      quantity = quantity - mangoCount;
      freeMango = "+" + mangoCount + " mangoes for free";
    }
  }
  return console.log(
    quantity +
      " mangoes for $" +
      price +
      " per unit = $" +
      quantity * price +
      "; " +
      freeMango
  );
}

mango(3, 3);
